body {
    font-family: Arial, sans-serif;
    text-align: center;
    margin: 50px;
    background-color: #f0f0f0;
}

h1 {
    color: #333;
}

.recipe {
    margin-bottom: 20px;
}

.recipeButton {
    background-color: #4CAF50;
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 12px;
    transition-duration: 0.4s;
}

.recipeButton:hover {
    background-color: #45a049;
}

.recipeContent {
    display: none;
    text-align: left;
    background-color: #fff;
    padding: 20px;
    border-radius: 12px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.recipeContent h2 {
    color: #4CAF50;
}

.recipeContent p, .recipeContent ul {
    margin-bottom: 10px;
}

.recipeContent ul {
    list-style-type: none;
}

